.suitepress-download-button:not(.suitepress-download-button-editor) .download-messages .error-message {
  padding: 15px 20px;
  border-radius: 8px;
  font-weight: 500;
  text-align: center;
}
.suitepress-download-button-editor:not(.suitepress-download-button-editor) .download-messages .success-message,
.suitepress-download-button:not(.suitepress-download-button-editor) .download-messages .success-message {
  background: #d1e7dd;
  color: #0f5132;
  border: 1px solid #badbcc;
}
.suitepress-download-button-editor:not(.suitepress-download-button-editor) .download-messages .error-message,
.suitepress-download-button:not(.suitepress-download-button-editor) .download-messages .error-message {
  background: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 768px) {
  .suitepress-download-button .lead-capture-form {
    padding: 20px;
    margin: 0 10px;
  }
  .suitepress-download-button .lead-capture-form .form-actions {
    flex-direction: column;
  }
  .suitepress-download-button .suitepress-download-trigger {
    padding: 12px 20px;
    font-size: 16px;
  }
}
.suitepress-download-trigger.loading {
  pointer-events: none;
  opacity: 0.7;
}
.suitepress-download-trigger.loading::before {
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid transparent;
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}
.suitepress-download-trigger.loading::after {
  content: "Preparing download...";
  margin-left: 0;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media (prefers-contrast: high) {
  .suitepress-download-button .suitepress-download-trigger {
    border: 2px solid currentColor;
  }
  .suitepress-download-button .lead-capture-form {
    border: 2px solid currentColor;
  }
}
@media (prefers-reduced-motion: reduce) {
  .suitepress-download-button .suitepress-download-trigger,
  .suitepress-download-button .btn-submit {
    transition: none;
  }
  .suitepress-download-button .suitepress-download-trigger:hover,
  .suitepress-download-button .btn-submit:hover {
    transform: none;
  }
  .suitepress-download-button .lead-capture-form {
    animation: none;
  }
}